import { Calendar, Header } from "rsuite";

const CalendarUkr = {

  months: {
    1: 'Січень',
    2: 'Лютий',
    3: 'Березень',
    4: 'Квітень',
    5: 'Травень',
    6: 'Червень',
    7: 'Липень',
    8: 'Серпень',
    9: 'Вересень',
    10: 'Жовтень',
    11: 'Листопад',
    12: 'Грудень',
  },

  sunday: 'ВС',
  monday: 'ПН',
  tuesday: 'ВТ',
  wednesday: 'СР',
  thursday: 'ЧТ',
  friday: 'ПТ',
  saturday: 'СБ',
  ok: 'OK',
  today: 'Сьогодні',
  yesterday: 'Вчора',
  hours: 'Годин',
  minutes: 'Хвилин',
  seconds: 'Секунд',
  /**
   * Format of the string is based on Unicode Technical Standard #35:
   * https://www.unicode.org/reports/tr35/tr35-dates.html#Date_Field_Symbol_Table
  **/
 formattedMonthPattern: 'MMM yyyy',
 formattedDayPattern: 'dd MMM yyyy',
  
};

const DeadLineCalendar = ({}) => {
  return (
    <>
      <Header>
        <h2 style={{color:'white', marginTop:"20px", marginLeft:"15px"}}>Календар виконання завдань</h2>
      </Header>
      <Calendar bordered isoWeek="true" locale={CalendarUkr}  style={{color:"white", marginTop:"20px"}}></Calendar>
    </>
  );
};

export default DeadLineCalendar;
